var swiper = new Swiper('.mySwiper', {
  slidesPerView: 1,
  spaceBetween: 10,
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
  breakpoints: {
    '@0.00': {
      slidesPerView: 1,
      spaceBetween: 10,
    },
    '@0.75': {
      slidesPerView: 2,
      spaceBetween: 20,
    },
    '@1.00': {
      slidesPerView: 3,
      spaceBetween: 40,
    },
    '@1.50': {
      slidesPerView: 4,
      spaceBetween: 50,
    },
  },
})

var swiper = new Swiper('.mySwiper', {
  slidesPerView: 'auto',
  centeredSlides: true,

  slidesPerView: 1,
  spaceBetween: 24,
  pagination: {
    el: '.myswiper-pagination',
    clickable: true,
  },
  breakpoints: {
    1024: {
      slidesPerView: 3,
      spaceBetween: 24,
      centeredSlides: true,
    },
  },
})
//瀑布流//
$('.row-Masonry')
  .imagesLoaded()
  .progress(function () {
    $('.row-Masonry').masonry()
  })
